<template>
  <v-container id="setup" class="d-flex flex-column grey lighten-3" fill-height>
    <v-spacer></v-spacer>

    <div>
      <h4 class="text-center title">{{ $t('buildings') }}</h4>
      <v-row class="d-flex flex-row justify-around mx-12 my-3">
        <v-btn
          class="mx-auto"
          small
          tile
          text
          v-for="construcao in construcoes"
          :key="construcao"
        >
          {{ construcao }}
        </v-btn>
      </v-row>

      <v-row>
        <v-btn
          class="mx-auto"
          dark
          depressed
          tile
          small
          @click="construcoes = sortearConstrucoes()"
          >{{ $t('shuffle-again')}}</v-btn
        >
      </v-row>
    </div>

    <v-spacer></v-spacer>
    
    <v-container class="d-flex">
      <v-btn
        class="ml-auto"
        dark
        tile
        depressed
        :to="{ name: 'Gameplay', params: { dificuldade: dificuldade } }"
        >{{ $t('start') }}</v-btn
      >
    </v-container>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    dificuldade: "",
    construcoes: []
  }),
  created() {
    this.dificuldade = this.$route.params.dificuldade;
    this.construcoes = this.sortearConstrucoes();
  },
  methods: {
    alerta(msg) {
      alert(msg);
    },
    sortearConstrucoes() {
      var opcoes = ["a", "b"];
      var l = [];
      for (var i = 1; i <= 10; i++) {
        var r = Math.round(Math.random());
        l.push(i + opcoes[r]);
      }
      return l;
    }
  }
};
</script>
