<template>
  <v-container
    id="gameplay"
    class="grey lighten-3 d-flex flex-column justify-center py-0"
    fill-height
  >
    <v-row justify="center">
      <v-dialog v-model="mostrarEntrega" persistent>
        <v-card>
          <v-card-title>{{ $t('deliver') }}</v-card-title>
          <v-card-text>            
            {{ $t('put-a-disc') }} <strong>{{ cidades[0].nome }}</strong>.
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="realizarEntrega()"
              >Ok</v-btn
            >
            <v-btn color="green darken-1" text @click="mostrarEntrega = false"
              >Cancelar</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>

    <card01
      v-if="corrente == 1"
      :entregaFeita="entregaFeita"
      :trabalhadorEscolhido="trabalhadorEscolhido"
      :vaq="vaqueiros > 0"
      :con="construtores > 0"
      :maq="maquinistas > 0"
      @usarTrabalhador="usarTrabalhador($event)"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
    >
    </card01>

    <card02
      v-if="corrente == 2"
      :entregaFeita="entregaFeita"
      :trabalhadorEscolhido="trabalhadorEscolhido"
      @usarTrabalhador="pegarTrabalhador($event)"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
    >
    </card02>

    <card-recolher
      v-if="corrente == 3"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="$t('take-the-expensive-cattle')"
    >
      <v-img
        class="mx-2"
        height="150"
        width="200"
        contain
        aspect-ratio="157/242"
        src="../assets/gadocaro.png"
        alt="gado caro"
      ></v-img>
    </card-recolher>

    <card-recolher
      v-if="corrente == 4"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="$t('take-the-cheap-cattle')"
    >
      <v-img
        class="mx-2"
        height="150"
        width="200"
        contain
        aspect-ratio="157/242"
        src="../assets/gadobarato.png"
        alt="gado barato"
      ></v-img>
    </card-recolher>

    <card-recolher
      v-if="corrente == 5"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="$t('take-the-teepee')"
    >
      <v-img
        class="mx-2"
        height="99"
        width="86"
        contain
        aspect-ratio="86/99"
        src="../assets/cabana.png"
        alt="cabana"
      ></v-img>
    </card-recolher>

    <card-recolher
      v-if="corrente == 6"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="$t('take-the-hazard')"
    >
      <v-img
        class="mx-2"
        height="99"
        width="86"
        contain
        aspect-ratio="86/99"
        src="../assets/desastre.png"
        alt="desastre"
      ></v-img>
    </card-recolher>

    <card-recolher
      v-if="corrente == 7"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="fraseConstrucao"
    >
      <v-img
        class="mx-2"
        height="99"
        width="86"
        contain
        aspect-ratio="86/99"
        src="../assets/construcao.png"
        alt="construcao"
      ></v-img>
    </card-recolher>

    <card-movimento
      v-if="corrente == 8"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :movimentos="movimentos"
    >
    </card-movimento>

    <card-recolher
      v-if="corrente == 9"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
      :msg="$t('take-the-objective')"
    >
      <v-img
        class="mx-2"
        height="150"
        width="200"
        contain
        aspect-ratio="157/242"
        src="../assets/objetivo.png"
        alt="objetivo"
      ></v-img>
    </card-recolher>

    <card-movimento
      v-if="corrente == 10"
      :entregaFeita="entregaFeita"
      @proximo="comprar()"
      @entrega="mostrarEntrega = true"
    >
    </card-movimento>
  </v-container>
</template>

<script>
import Card01 from "../components/Card01.vue";
import Card02 from "../components/Card02.vue";
import CardMovimento from "../components/CardMovimento.vue";
import CardRecolher from "../components/CardRecolher.vue";

export default {
  data: () => ({
    deck: [1, 1, 2, 2, 3, 4, 5, 6, 7, 8, 8, 9, 10, 10, 10, 10],
    descarte: [],
    construcoes: [],
    ultimaConstrucao: "",
    vaqueiros: 0,
    construtores: 0,
    maquinistas: 0,
    movimentos: 3,
    cidades: [
      { nome: "Wichita", preto: false },
      { nome: "Colorado Springs", preto: false },
      { nome: "Santa Fe", preto: false },
      { nome: "Albuquerque", preto: true },
      { nome: "El Passo", preto: true },
      { nome: "San Diego", preto: false },
      { nome: "Sacramento", preto: true },
      { nome: "San Francisco", preto: true }
    ],
    entregas: [],
    mostrarEntrega: false,
    entregaFeita: false,
    trabalhadorEscolhido: false
  }),
  components: {
    card01: Card01,
    card02: Card02,
    "card-movimento": CardMovimento,
    "card-recolher": CardRecolher
  },
  created() {
    var dificuldade = this.$route.params.dificuldade;
    if (dificuldade === "fácil") {
      this.inicioFacil();
    } else if (dificuldade === "médio") {
      this.inicioMedio();
    } else {
      this.inicioDificil();
    }
    this.embaralhar();
    this.comprar();
  },
  computed: {
    corrente() {
      if (this.descarte.length === 0) {
        return null;
      }
      return this.descarte[this.descarte.length - 1];
    },
    fraseConstrucao() {
      return this.$t('put-a-building').replace(
        "XXX",
        this.ultimaConstrucao
      );
    }
  },
  methods: {
    inicioFacil() {
      for (var i = 1; i <= 5; i++) {
        this.construcoes.push(i + "a");
      }      
    },
    inicioMedio() {
      for (var i = 1; i <= 10; i++) {
        this.construcoes.push(i + "a");
      }
      this.deck.pop();
      this.cidades.splice(0, 1);
    },
    inicioDificil() {
      for (var i = 6; i <= 10; i++) {
        this.construcoes.push(i + "a");
      }
      this.deck.pop();
      this.deck.pop();
      this.cidades.splice(0, 2);
    },
    embaralhar() {
      var aux = [];
      while (this.deck.length > 0) {
        var r = Math.floor(Math.random() * this.deck.length);
        var carta = this.deck[r];
        this.deck.splice(r, 1);
        aux.push(carta);
      }
      this.deck = aux;
    },
    comprar() {
      this.entregaFeita = false;
      this.trabalhadorEscolhido = false;

      if (this.deck.length == 0) {
        this.deck = this.descarte;
        this.descarte = [];
        this.embaralhar();
      }

      var carta = this.deck.pop();
      this.descarte.push(carta);

      if (carta == 7) {
        this.ultimaConstrucao = this.pegarConstrucao();
      }

      console.log(
        "Deck: ",
        this.deck.length,
        " + Descarte: ",
        this.descarte.length,
        " = ",
        this.deck.length + this.descarte.length
      );
    },
    pegarTrabalhador(trabalhador) {
      if (trabalhador === "vaqueiro") {
        this.vaqueiros++;
      } else if (trabalhador === "construtor") {
        this.construtores++;
      } else if (trabalhador === "maquinista") {
        this.maquinistas++;
      }

      this.trabalhadorEscolhido = true;
    },
    usarTrabalhador(trabalhador) {
      if (trabalhador === "vaqueiro") {
        this.vaqueiros--;
      } else if (trabalhador === "construtor") {
        this.construtores--;
      } else if (trabalhador === "maquinista") {
        this.maquinistas--;
      }

      this.trabalhadorEscolhido = true;
    },
    pegarConstrucao() {
      if (this.construcoes.length == 0) {
        return "";
      }

      var r = Math.floor(Math.random() * this.construcoes.length);
      var construcao = this.construcoes[r];
      this.construcoes.splice(r, 1);
      console.log(this.construtores);
      return construcao;
    },
    realizarEntrega() {
      var cidade = this.cidades[0];
      if (this.cidades.length > 1) {
        this.cidades.splice(0, 1);
        this.entregas.push(cidade);
      }
      if (cidade.preto && this.movimentos < 5) {
        this.movimentos++;
      }

      this.entregaFeita = true;
      this.mostrarEntrega = false;
    }
  }
};
</script>
