<template>
  <v-container
    id="home"
    class="d-flex flex-column grey lighten-4 justify-space-between"
    fill-height
  >
    <div class="d-flex flex-column">
      <div class="d-flex justify-center">
        <v-btn class="ma-1" tile depressed small v-for="language in languages" :key="language.code" @click="changeLanguage(language.code)">
          <flag :iso="language.flag" class="mr-1" /> 
          {{ language.title }}
        </v-btn>
      </div>

      <h3 class="text-center title">
        {{ $t('welcome') }}
      </h3>
      <v-btn
        class="mx-auto my-3"
        dark
        tile
        depressed
        @click="installer()"
        :style="{ display: installBtn }"
        >{{ $t('install') }}</v-btn
      >
    </div>

    <div class="d-flex flex-column">
      <h3 class="subtitle">{{ $t('dificulty-level') }}</h3>
      <v-radio-group v-model="selecionada">
        <v-radio :label="$t('easy')" value="fácil" color="green"></v-radio>
        <v-radio :label="$t('medium')" value="médio" color="yellow"></v-radio>
        <v-radio :label="$t('hard')" value="difícil" color="red"></v-radio>
      </v-radio-group>
    </div>

    <div class="d-flex flex-column">
      <v-btn
        class="ml-auto"
        dark
        tile
        depressed
        :to="{ name: 'Setup', params: { dificuldade: selecionada } }"
        >{{ $t('next') }}</v-btn
      >
    </div>
  </v-container>
</template>

<script>
import i18n from "../i18n";

export default {
  data: () => ({
    dificuldades: ["fácil", "médio", "difícil"],
    selecionada: "fácil",
    installer: undefined,
    installBtn: "none",
    languages: [
      {flag: 'br', code: 'pt-br', title: 'Português'},
      {flag: 'us', code: 'en', title: 'English'},
    ]
  }),
  created() {
    console.log("CREATED HOME");
    let installPrompt;

    window.addEventListener("beforeinstallprompt", e => {
      console.log("beforeInstallPrompt");
      e.preventDefault();
      installPrompt = e;
      this.installBtn = "block";
    });

    this.installer = () => {
      this.installBtn = "none";
      installPrompt.prompt();
      installPrompt.userChoice.then(result => {
        if (result.outcome === "accepted") {
          console.log("User accepted");
        } else {
          console.log("User denied");
        }
        installPrompt = null;
      });
    };
  },
  methods: {
    changeLanguage(code) {
      i18n.locale = code;
    }
  }  
};
</script>
